.input-group {
  width: 100%;
  /* border: 1px solid green; */
}

.input-group__input {
  width: 85%;
  height: 2.5rem;
  border: 1px solid #d9d9d9;
  border-right: none;
  border-radius: 5px 0px 0px 5px;
}

.input-group__button {
  width: 15%;
  height: 2.5rem;
  border: 1px solid #d9d9d9;
  border-radius: 0px 5px 5px 0px;
  background-color: #1890FF;
  color: white;
  font-size: 1rem;
  cursor: pointer;
}

.input-group__button:hover {
  background-color: rgb(64, 169, 255);
  color: white;
  font-size: 1rem;
  cursor: pointer;
}